.advanced-learn {
    display: flex;
    flex-direction: column;
    
    margin: 0 20px;
    margin-bottom: 75px; 
 
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    background-color: #ffffff;

}

.advanced-learn__image {
    margin: 0;
    padding: 0;
    height: 200px;

    border-top: transparent;
    border-radius: 10px 10px 0 0;

    
    background-image: url(../../img/IMG_8722.JPG);
    background-position: center 65%;
    background-size: cover;  
    background-repeat: no-repeat;
}   

.advanced-learn__title {
    font-style: 20px;
    line-height: 24px;
    font-weight: 700;
    text-align: center;   

    margin: 20px 0;
    padding: 0;
    width: 100%;
}

.advanced-learn-list {
    list-style: none;
    margin: 0;
    padding: 0 25px;  
    padding-left: 30px;
}

.advanced-learn-list__item {
    margin: 10px 0;
    position: relative;
    

    &::after {
        content: '';

        position: absolute;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 1px;
        background-color: #000000;
        opacity: 0.2; 
    }
}

.advanced-learn__text {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 300;
    color: #000000;
    text-align: center;

    margin: 25px; 
}

.advanced-learn__price {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 400;
    font-size: 20px;
    color: #000000;
    text-align: center;

    margin: 25px; 
}

.advanced-learn__details { 
    display: block;
    box-sizing: border-box;

    width: 260px;
    height: 50px;
    margin: 50px auto;
    padding: 15px;

    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-align: center;
    text-transform: uppercase;

    color: #ffffff;
    background-color: #222222;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;

    cursor: pointer; 
}

@keyframes bounce {
    0%   { transform: translateY(-2000px); }
    50%  { transform: translateY(50px)   ; }
    75%  { transform: translateY(-10px)  ; }
    100% { transform: translateY(0)      ; }
}

@keyframes shake {
    0%, 100% { transform: translateX(0) ;    }
    30%, 70% { transform: translateX(-20px); }
    50%      { transform: translateX(20px);  }
}

@keyframes close {
    0%   { transform: translateY(0); }
    50%  { transform: translateY(-10px)   ; }
    75%  { transform: translateY(50px)  ; }
    100% { transform: translateY(-2000px)      ; }
}

.advanced-learn__modal-details {
    display: none;
    position: fixed;
    width: 90vw;
    height: 96vh;
    top: 2vh; 
    left: 5vw; 
    z-index: 10;

    padding: 0;
    margin: 0;

    border-radius: 4vw;
    background-color: #ffffff;
    box-shadow: 0px 16px 20px rgba(0,1,1,0.75); 
}


.advanced-learn__modal-details--close {
    width: 4vw;
    height: 4vw; 
    margin-top: 5vh;
    margin-left: 80vw;

    padding: 0;
    border: 0;
    
    background-color: transparent;  
    opacity: 0.9;

}

.advanced-learn__modal-details__text {
    font-family: 'Ubuntu',Arial,sans-serif;
    font-weight: 300;
    color: #000000;
    text-align: center;

    margin-top: 2vh;
}

.advanced-learn__modal-details__image {
    margin-top: 5vh;
    padding: 0;
    height: 40vh;

    border-top: transparent;
    border-radius: 0;

    
    background-image: url(../../img/IMG_8722.JPG);
    background-position: center 65%;
    background-size: cover;  
    background-repeat: no-repeat;
} 

.advanced-learn__modal-details__buy { 
    display: block;
    box-sizing: border-box;

    width: 260px;
    height: 50px;
    margin: 7vh auto;
    padding: 15px;

    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    letter-spacing: 0.5px;
    text-align: center;
    text-transform: uppercase;

    color: #ffffff;
    background-color: #222222;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;

    cursor: pointer; 
}


.advanced-learn__modal-details--modal-close {
    
    animation-name: close;
    animation-duration: 2s;
    
    
}

.advanced-learn__modal-details--modal-show {
    display: block;
    animation-name: bounce;   
    animation-duration: 2s; 
}

.advanced-learn__modal-details--modal-error {
    animation-name: shake;
    animation-duration: 2s;
}

